.nav{height:80px;width:100%;max-width:inherit;margin:0 auto;position:fixed;background:#fff;z-index:999}.nav:after{clear:both}.nav-header{float:left}.nav-header .nav-sitename{color:#424243;text-transform:uppercase;font-family:Oswald,sans-serif}.nav-menu{float:right;list-style-type:none;padding:0;margin:0}.nav-menu .nav-item{cursor:pointer;color:gray;line-height:80px;display:inline-block;margin:0 10px;font-family:Oswald,sans-serif}.nav-menu .nav-item a:visited{color:gray}.nav-menu .nav-item a:hover{color:#000}@media screen and (max-width:750px){.nav{padding:0 10px}}@media screen and (min-width:750px){.nav{padding:0 40px}}